.menu-item {
  display: flex;
  align-items: flex-start;
  width: 100%; /* Make sure the menu item takes full width if not already */
  margin-top: 11%;
}

.food-image {
  width: 110px; /* Adjust the width as needed */
  height: auto;
  border-radius: 10px;
  margin-right: 2%;
  margin-left: 2%;
}

.title {
  font-size: 1.1em;
  font-weight: bold;
  margin-bottom: 5px;
  margin-top: 0;
  margin-left: 3%;
}

.description {
  font-size: 1em; /* Adjust the font size as needed */
  margin-bottom: 25px;
  word-wrap: break-word;
  overflow-wrap: break-word;
  width: 70%; /* Set a specific width for the description */
  font-weight: lighter;
  margin-left: 3%;
}

.price {
  font-size: 1em;
  margin-left: auto; /* This will push the price to the right */
}


.header-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 10%; /* Adjust as needed */
  margin-bottom: 10%; /* Adds bottom margin */
}

.logo {
  width: 40%; /* Adjust as needed */
  margin-bottom: 2rem; /* Provides space between the logo and the text */
}

.subheading{
  color: orange;
  font-family: 'Pacifico', cursive;
  margin-top: 0px;
  font-size: 20px;

} 

.titleSub {
  margin-top: 5px;;
  font-family: 'Brush Script MT', cursive, sans-serif;
  font-size: 25px;
}
/* Ensure you align text and button to the layout as shown in the mockup */

.title-price {
  display: flex;
  justify-content: space-between; /* Pushes title and price apart */
  width: 95%; /* Ensures it stretches fully */
}

.item-info {
  display: flex;
  flex-direction: column;
  flex-grow: 1; /* Ensures it takes up the remaining space */
}

.d-flex.align-items-center {
  display: flex;
  justify-content: flex-end; /* Aligns the content to the right */
  width: 95%; /* Ensures the container stretches fully to allow right alignment */
}

.menu-item-image {
  margin-right: 2%;
  margin-left: 2%;
  align-self: flex-start; /* This makes sure the image container aligns at the top */
}

.menu-item-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between; /* This will push the quantity-controls towards the bottom */
  flex-grow: 1;
}

.quantity-controls {
  align-self: flex-end; /* Aligns quantity and buttons at the end of the item-info */
  display: flex;
  margin-top: auto; /* This pushes the controls to the bottom */
}

.minusButton, .plusButton{
  background-color: white;
  border: none;
  color: black;
  padding: 0; /* Remove padding to better control the size */
  width: 20px; /* Set a fixed width */
  height: 20px; /* Set a fixed height, equal to the width for a circle */
  border-radius: 50%; /* This makes the button circular */
  cursor: pointer;
  font-size: 1em; /* Adjust if needed to fit the button size */
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px; /* Adjust spacing between buttons */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); /* Optional: Adds a subtle shadow effect */
  border: 1px solid black; /* Adds a 1px black border */
}

/* Additional styles to ensure the buttons don't stretch */
.minusButton, .plusButton {
  flex: 0 0 auto; /* Prevents the buttons from stretching and keeps them at their set width and height */
}

.plusButton {
  margin-right: 0.5em; /* Relative margins */
}

.checkout-container {
  display: flex;
  align-items: center; /* Vertically center align the items */
  justify-content: flex-end; /* Align the container contents to the right */
  margin-top: 20px; /* Adjust as needed */
  margin-right: 2%; /* Adds margin to the right side */
  margin-bottom: 10%; /* Keep consistent with your bottom margin */
}

.subtotal {
  /* Your existing styles for subtotal, make sure it includes margin-right for spacing */
  margin-right: 12%; /* Adjust as needed */
  font-size: 1.5em; /* Adjust as needed */
  margin-bottom: 10%;
}

.orderButton, .clearButton {
  padding: 5px 13px; /* Adjust padding to match the design */
  margin-left: 3%; /* This creates space between the buttons */
  border: none;
  border-radius: 20px; /* This creates the rounded corners */
  cursor: pointer;
  outline: none; /* Optional: Removes the outline on focus */
  font-size: 1em; /* Adjust as needed */
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.2); /* Optional: Adds shadow for depth */
  margin-bottom: 10%;
  border: 1px solid black; /* Adds a 1px black border */
  padding: 4px 12px; /* Adjust padding to account for the border */
}

.orderButton, .clearButton {
  flex: 0 0 auto; /* Prevents the buttons from stretching and keeps them at their set width and height */
}

.orderButton {
  background-color: #75a8d1; /* Or any other color you prefer */
  color: black;
}

.clearButton {
  background-color: #ced6dd; /* Or any other color you prefer */
  color: black;
}
